package com.springblossem.ouch.client

import com.springblossem.ouch.client.state.AppState
import com.springblossem.ouch.client.state.STORE
import com.springblossem.ouch.client.state.reducers.UpdateId
import com.springblossem.ouch.common.Auth
import com.springblossem.ouch.common.Quiddity
import kotlinx.browser.document
import react.FC
import react.create
import react.dom.client.createRoot
import react.dom.html.ReactHTML.button
import react.dom.html.ReactHTML.p
import react.redux.Provider
import react.redux.useDispatch
import react.redux.useSelector
import react.router.Route
import react.router.dom.BrowserRouter
import redux.RAction

fun main() {
  // setup react
  val container = document.getElementById("root")!!
  document.body!!.appendChild(container)
  val root = createRoot(container)
  println("root established")

  // setup redux
  val provider = Provider.create {
    store = STORE
    child(App.create())
  }

  // render
  root.render(provider)
}

val App = FC<Nothing> {
  val auth = useSelector<AppState, Auth?> { it.auth }
  val dispatch = useDispatch<RAction, Any?>()

  p {
    +(auth?.id?.toString() ?: "")
  }

  button {
    +"Update"
    onClick = {
      dispatch(UpdateId(1))
    }
  }
  // setup router
  BrowserRouter {
    Route {
      path = "/"
      element = Home.create { name = "Page1" }
    }
    Route {
      path = "/2"
      element = Home.create { name = "Page2" }
    }
  }
}
